import React from 'react';
import {Table} from 'react-bootstrap';
import { connect, useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';

function Cart() {

    let state = useSelector((state) => state)
    let dispatch = useDispatch();
    let history = useHistory();

    return (
    <>

    <Table striped bordered hover>
        <thead>
            <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경</th>
            </tr>
        </thead>
        <tbody>
           { state.reducer.map((e)=>{
               return (
                <tr>
                  <td>{e.id}</td>
                  <td onClick={()=>{history.push(`/detail/${e.id}`)}}>{e.name}</td>
                  <td>{e.quan}</td>
                  <td>
                    <button onClick={()=>{ dispatch({type: "수량증가", payload: e.id}) }}>+</button>
                    &nbsp;
                    <button onClick={()=>{ dispatch({type: "수량감소", payload: e.id}) }}>-</button>
                  </td>
                </tr>
               )
           })
           }
        </tbody>
    </Table>

    { state.reducer2 === false
        ? <div className="my-alert--yellow">
            <p>지금 구매하시면 20% 할인!</p>
            <button onClick={()=>{ dispatch({type: "닫기"}) }}>닫기</button>
          </div>
        : null
    }

    
    </>
    )
}

// ㅡㅡuseSelector, useDispatch 옛날 버전.
// function state를props화(store값) {
//   return {
//       state: store값.reducer,
//       off: store값.reducer2
//   }
// }
// export default connect(state를props화)(Cart);

export default Cart;